import React, { Component } from 'react';
import './app.css';
import Header from './header/header.jsx';
import Login from './auth/login.jsx';
class Auth extends Component {

  render() {
    return (
      <div id="main-container">
        <Header />
        <Login />
      </div>
    );
  }
}

export default Auth;



